import Home from './topPage/Home';
import Doc from './topPage/Doc';
import Tea from './topPage/Tea';
import Blog from './topPage/Blog';
import Soc from './topPage/Soc';
import Login from './topPage/Login';
import Core from './topPage/Core'
import Guide from './topPage/Guide'
import Api from './topPage/Api'
import Hooks from './topPage/Hooks'

const routes = [
    {
        path : '/',
        exact : true,
        component : Home
    },
    {
        path : '/doc',
        component : Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core,
            },
            {
                path:'/doc/guide',
                component:Guide,
            },
            {
                path:'/doc/api',
                component:Api,
            },
            {
                path:'/doc/hooks',
                component:Hooks,
            }
        ]     
    },
    {
        path : '/tea',
        component : Tea,
        pri:true
    },
    {
        path : '/blog',
        component : Blog
    },
    {
        path : '/soc',
        component : Soc
    },
    {
        path : '/login',
        component : Login
    }
]

