import React from 'react'
import {NavLink} from 'react-router-dom'
import './header.css'

function Header() {
    return (
        <header>
            <nav>
                <NavLink exact activeClassName='active' to='/'>React</NavLink>
                <NavLink activeClassName='active' to='/doc'>文档</NavLink>
                <NavLink activeClassName='active' to='/tea'>教程</NavLink>
                <NavLink activeClassName='active' to='/blog'>博客</NavLink>
                <NavLink activeClassName='active' to='/soc'>社区</NavLink>
            </nav>
        </header>
    )
}

export default Header
